.container {
  padding: 0 1.75rem;

  @media #{$mq-xsmall} {
     padding: 0;
    margin: 0 4%;
  }

  @media #{$mq-medium} {
    margin: 0 8%;
  }

  @media #{$mq-large} {
    margin: 0 12%;
  }

  @media #{$mq-xlarge} {
    margin: 0 18%;
  }

  &--tight {
      @media #{$mq-medium} {
        margin: 0 12%;
      }

      @media #{$mq-large} {
        margin: 0 16%;
      }

      @media #{$mq-xlarge} {
        margin: 0 20%;
      }
  }

  &--right {
      @media #{$mq-xsmall} {
        padding: 0;
        margin: 0;
        margin-left: 4%;
      }

      @media #{$mq-medium} {
        margin-left: 8%;
      }

      @media #{$mq-large} {
        margin-left: 12%;
      }

      @media #{$mq-xlarge} {
        margin-left: 18%;
      }
  }
}
