.value {
    &s-section {
        background-size: 100% 100%;
        background-position: top center;
        background-repeat: no-repeat;
        padding: 6rem 0;

        &__title {
            text-align: center;
            color: $simply-green--light;
            margin-bottom: 4rem;
        }
    }
    $value: &;
    background-color: $white;
    box-shadow: 0 1rem 4rem 0 rgba(29, 49, 38, 0.33);
    margin-bottom: 4rem;
    @media #{$mq-xsmall} {
        display: flex;
        align-items: center;
    }

    &:nth-of-type(odd) {

        #{$value}__img {
            order: 2;
        }

        #{$value}__content {
            order: 1;
        }
    }

    &__img {
        @media #{$mq-xsmall} {
            flex: 1 1 40%;
            max-width: 40%;
            overflow: hidden;

            img {
                width: 100%;
                min-height: 350px;
                min-width: 350px;
            }
        }
        @media #{$mq-large} {
            flex: 1 1 33.333%;
            max-width: 33.333%;
        }
    }

    &__content {
        padding: 1.5rem;
        @media #{$mq-xsmall} {
            padding: 1rem;
            flex: 1 1 60%;
            max-width: 60%;
        }
        @media #{$mq-small} {
            padding: 2rem;
        }
        @media #{$mq-medium} {
            padding: 2.5rem;
        }
        @media #{$mq-large} {
            padding: 2rem 3rem;
            flex: 1 1 66.666%;
            max-width: 66.666%;
        }
        @media #{$mq-xlarge} {
            padding: 4rem;
        }
    }

    &__title {
        color: $simply-green--dark;
        position: relative;
        display: block;
        font-size: $font-size-h2;
        font-weight: 600;
        margin-bottom: 2rem;
        @media #{$mq-xsmall} {
            font-size: $font-size-h3;
        }
        @media #{$mq-small} {
            font-size: $font-size-h2;
        }
        @media #{$mq-medium} {
            font-size: $font-size-h2*1.2;
            line-height: $header-line-height*1.2;
        }
        @media #{$mq-large} {
            font-size: $font-size-h2*1.3;
        }
        @media #{$mq-xlarge} {
            font-size: $font-size-h2*1.4;
        }
        @media #{$mq-xxlarge} {
            font-size: $font-size-h2*1.6;
        }

        &:after {
            content: '';
            width: 3rem;
            height: 3px;
            background-color: $simply-green--light;
            position: absolute;
            bottom: -1rem;
            left: 0;
        }
    }
}
