.footer {

    &-border {
        padding-top: 3rem;
        overflow-x: hidden;

        img {
            min-width: 1280px;
        }
    }

    padding: 3rem 0;

    @media #{$mq-medium} {
        padding: 4rem 0;
    }

    @media #{$mq-large}  {
        padding: 5rem 0;
    }

    background-color: $gray-dark;
    color: $white;

    a {
        color: $gray-light;
        text-decoration: none;

        &:hover,
        &:focus {
            color: darken($gray-light, 20%);
        }

        &:visited {
            color: $gray-light;
        }
    }

    font-size: $font-size-sm;

    &-menu {
        list-style: none;
        margin: 0;
        padding: 0;
        text-align: center;
        font-weight: 600;

        @media #{$mq-small}  {
            text-align: left;
            font-size: .75rem;
            flex: 1 1 25%;
            max-width: 25%;
            padding: 0 1rem;
        }

        @media #{$mq-large}  {
            font-size: .875rem;
        }

        &__heading {
            text-transform: uppercase;
            font-weight: 600;
            letter-spacing: 1px;
            font-size: 11px;

            @media #{$mq-large}  {
                font-size: .875rem;
            }

            @media #{$mq-xlarge}  {
                font-size: 1rem;
            }
        }

        &__item {
            margin-bottom: .5rem;
        }
    }

    &-links {

        @media #{$mq-medium}  {
            display: flex;
            padding-bottom: 2rem;
        }

        &__about {
            display: none;

            @media #{$mq-small}  {
                display: block;
                text-align: center;
                margin-bottom: 2rem;
            }

            @media #{$mq-medium}  {
                text-align: left;
                margin-bottom: 0;
                flex: 1 1 41.666667%;
                max-width: 41.666667%;
                font-size: .75rem;
                padding-right: 1.5rem;
            }

            @media #{$mq-large}  {
                font-size: .875rem;
            }

            @media #{$mq-xlarge}  {
                font-size: 1rem;
            }
        }

        &__menus {
            @media #{$mq-small}  {
                display: flex;
                margin-left: -1rem;
                margin-right: -1rem;
            }

            @media #{$mq-medium}  {
                margin-left: 0;
                margin-right: 0;
                flex: 1 1 50%;
                max-width: 50%;
            }
        }

        &__social {
            padding: 2rem 0;

            @media #{$mq-medium}  {
                flex: 1 1 8.333333%;
                max-width: 8.333333%;
                padding: 0;
                padding-left: 1.5rem;
            }
        }

        &__text {
            margin-bottom: 0;
        }
    }

    .social-menu {



        &__list {
            list-style: none;
            padding: 0;
            margin: 0;
            display: flex;
            justify-content: center;

            @media #{$mq-medium}  {
                flex-direction: column;
            }

            .facebook,
            .linkedin,
            .twitter {
                width: 1.5rem;
                height: 1.5rem;

                path {
                    fill: $gray-light;
                }
            }
        }

        &__item {
            margin-right: 1.5rem;

            @media #{$mq-medium}  {
                margin-right: 0;
                margin-bottom: 1.5rem;

                &:last-child {
                    margin-bottom: 0;
                }
            }

            &:last-child {
                margin-right: 0;
            }
        }

        &__link {
            border: 1px solid $gray-light;
            border-radius: 100%;
            width: 2.5rem;
            height: 2.5rem;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &-bottom {
        text-align: center;
        border-top: 1px solid $gray-light;
        color: $gray-light;
        font-size: .75rem;
        padding-top: 2rem;

        @media #{$mq-small}  {
            display: flex;
            align-items: flex-end;
        }

        @media #{$mq-medium}  {
            font-size: .875rem;
        }

        @media #{$mq-xlarge}  {
            font-size: 1rem;
        }

        &__logo {

            &-link {
                display: inline-block;
            }

            @media #{$mq-small}  {
                flex: 1 1 50%;
                max-width: 50%;
                text-align: left;
            }
        }

        .sysco {
            width: 100px;
        }

        &__terms {

            @media #{$mq-small}  {
                flex: 1 1 50%;
                max-width: 50%;
                text-align: right;
            }
        }

        &__rights {
            font-style: italic;

            @media #{$mq-small}  {
                margin-bottom: 0;
        }
    }
}
}
