.hero {
    position: relative;
    background-color: $white;
    height: 800px;
    position: relative;

    &__img {
        min-width: 1920px;
        position: absolute;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        height: 100%;
        margin: 0 auto;
        width: 100%;
        top: 0;
        left: 50%;
        transform: translateX(-50%)
    }



        display: flex;
        align-items: center;
        justify-content: center;


    @media #{$mq-xlarge} {
        height: 50vw;
    }




    &__content {
        position: relative;
        margin: 0 auto;
        text-align: center;
    }

    &__title {
        color: $white;
        text-shadow: 0px 1rem 2rem rgba(29, 49, 38, 0.5);
        font-size: 5rem;

        @media #{$mq-xsmall} {
            font-size: 6.5rem;
        }

        @media #{$mq-large} {
            font-size: 7vw;
            text-shadow: 0px 1vw 2vw rgba(29, 49, 38, 0.5);
            margin: 0;
        }
    }

    &__subtitle {

        font-size: 1.5rem;
        color: $simply-green--dark;
        font-weight: 700;

        @media #{$mq-xsmall} {
            font-size: 1.75rem;
        }

        @media #{$mq-large} {
            font-size: 2vw;

            margin: 0;
        }

    }
}
