.resources {
    padding: 6rem 0;
    background-color: $simply-gray--dark;
    background-image: url('../assets/img/pattern-gray.jpg');
    background-size: 33.333%;
    background-position: left center;
    background-repeat: no-repeat;

    &__heading {
        color: $white;

        margin-bottom: 3rem;

        @media #{$mq-small} {
            display: flex;
            align-items: center;
        }
    }

    &__title,
    &__lead {

        @media #{$mq-small} {
        flex: 1 1 50%;
        max-width: 50%;

        }
    }

    &__lead {
        font-weight: 700;

        @media #{$mq-small} {
        padding: 0 $gutter; 

        }
    }
}

.resource {

    &__img {
        margin-bottom: .5rem;
        width: 100%;
    }


    &__title {
        display: block;
        color: $simply-green--light;
        margin-bottom: .5rem;
    }

    &__text {
        color: $white;
        margin-bottom: 1rem;
    }

    .button {
        margin-right: 0;
    }

}
