.video {

    $video: &;

    &-section{
    position: relative;
    background-color: $white;

    padding: 6rem 0;

    @media #{$mq-small} {
        background-image: url('../assets/img/background-tomato.jpg');
        background-size: 90%;
        background-position: center center;
        background-repeat: no-repeat;
    }

    &__title {
        color: $simply-green--light;
        text-align: center;
        margin-bottom: 4rem;
    }
}



    &-grid {

        margin-left: -$gutter;
        margin-right: -$gutter;
        position: relative;

        @media #{$mq-small} {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: flex-end;
        }
    }

    &__item {
        padding: 0 $gutter;
        margin-bottom: $gutter*2;

        @media #{$mq-small} {
            flex: 1 1 33.333%;
            max-width: 33.333%;
        }

        @media #{$mq-medium} {
            border-width: 2rem;
            flex: 1 1 40%;
            max-width: 40%;
        }
    }

    &__item:first-of-type {
        @media #{$mq-medium} {
            flex: 1 1 60%;
            max-width: 60%;
        }
    }

    &__item:last-of-type {
        @media #{$mq-medium} {
            margin-left: 15%;
        }
    }

    &__item:nth-of-type(2) {
        @media #{$mq-medium} {
            padding-bottom: 8rem;
        }
    }

    &__box {
        padding: 1rem;
        border: 1rem solid $white;
        background-color: $simply-gray--dark;
        height: 340px;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        box-shadow: 0 1rem 4rem 0 rgba(29, 49, 38, 0.33);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        @media #{$mq-medium} {
            padding: 2rem;
            border-width: 2rem;
            height: 30vw;
        }
    }

}
