.value-list {
    @extend .list--reset;

    &__item {
        font-size: 1.5rem;
        display: flex;
        align-items: flex-start;
        padding: 1.25rem 0;
        border-bottom: 1px solid $simply-green--dark;

        &:last-child {
            border: 0;
        }

        p {
            margin-bottom: 0;
        }

        @media #{$mq-medium} {
            padding: 1.75rem 0;
        }
    }

    &__icon {
        width: 2.25rem;
        margin-right: 1rem;
    }
}
