.card {

    background-color: $white;
    width: 100%;

    $card: &;

    &--dark {
        background-color: $gray-dark;
    }

    &--shadow {
        box-shadow: 0 1rem 4rem 0 rgba(29, 49, 38, 0.33);
    }

    &--inline {

        @media #{$mq-small} {
            display: flex;
            flex-direction: row;
            align-items: stretch;
        }

        #{$card}__img {
            @media #{$mq-small} {
                flex: 1 1 33.33%;
                max-width: 33.33%;
            }
        }
        #{$card}__content {

            @media #{$mq-small} {
                flex: 1 1 66.66%;
                max-width: 66.66%;
                padding: 1rem 1.5rem;
            }

            @media #{$mq-medium} {
                padding: 1.5rem 3rem;
            }

            @media #{$mq-xlarge} {
                padding: 3rem;
            }


        }

        &:nth-of-type(odd) {

            @media #{$mq-small} {
                #{$card}__content {
                    order: 1;
                }
                #{$card}__img {
                    order: 2;
                }
            }
        }

    }

    &__img {
        width: 100%;
    }

    &__title {
        display: block;
        @extend .heading--4;
    }

    &__content {
        padding: 1.5rem;
    }
}
