.form {
    padding: 2rem;
    display: block;
    background-color: $white;
    min-height: 440px;

    @media #{$mq-large} {
        padding: 3rem 2.5rem;
    }

    &__group {

        margin-bottom: 1.5rem;


        label, input {
            display: block;
        }

        label {
        margin-bottom: .5rem;
        }


        input {
            width: 100%;
            padding: .75rem .5rem;
            border: 0;
            border-bottom: 1px solid $gray-dark;
            border-radius: 0;
        }
    }

    &__check {

        input {
            width: auto;
            margin-right: .5rem;
        }

    }

    .button {
        margin-top: 2rem;
    }
}
