.product {

    &-videos {
        position: relative;
        padding: 3rem 0;

        @media #{$mq-small} {
            margin-top: -12rem;
            z-index: 1;
            padding-top: 0; 
        }

        &__title {
            display: block;
            margin-bottom: 3rem;
            position: relative;

            &:after {
                content: '';
                position: absolute;
                left: 0;
                bottom: -1.5rem;
                width: 4rem;
                height: 3px;
                background-color: $simply-green--light;
            }
        }
    }

    &-video__img {
        display: block;
        margin-bottom: 1.5rem;
        box-shadow: 0 1rem 1.5rem 0 rgba(29, 49, 38, 0.33);
    }

}
