.site-header {
    top: 0;
    left: 0;
    width: 100%;
    background-color:$white;

    .logo {

        &__blue {
            fill: $sysco-blue;
        }

        &__green {
            fill: $sysco-green;
        }
    }

    .nav {

        &__toggle {

            .icon-bar {
                background-color: $sysco-blue;
            }
        }

        &__menu {

            &--primary {

                @media #{$mq-medium} {

                    > .nav__item {

                        .nav__link {
                            color: $sysco-blue;

                            &:visited {
                                color: $sysco-blue;
                            }
                        }
                    }
                }
            }
        }
    }

    &.js-is-fixed,
    &.js-is-open {
        position: fixed;
        width: 100%;
        z-index: 10;
        top: 0;
        left: 0;
        box-shadow: 0 0.2rem 1rem 0 rgba(0, 0, 0, 0.25);
    }
}
