.product {

    &-hero {

        .container {
            position: relative;
            z-index: 1;
        }

        &__img {
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            img {
                box-shadow: 0 1rem 4rem 0 rgba(29, 49, 38, 0.33);
            }

            .play {
                position: absolute;
            }

            @media #{$mq-small} {
                order: 2;
            }
        }

        &__text {

            @media #{$mq-small} {
                padding-bottom: 2rem;
                order: 1;
            }
        }

        &__title {
            color: $gray-light;
        }

        &__subtitle {
            color: $simply-green--dark;
            font-weight: 700;
        }
    }
}
