.intro {
    padding: 3rem 0;
    color: $gray-dark;
    background-color: $white;

    @media #{$mq-small} {
        padding: 6rem 0;
    }

    p:last-child {
        margin: 0;
    }

    &__box {
        text-align: center;
    }

    &__logo {
        width: 100%;
        max-width: 280px;
        margin: 0 auto;
        margin-bottom: 2rem;

        @media #{$mq-xsmall} {
            max-width: 55%;
        }

        @media #{$mq-small} {
            max-width: none;
        }
    }

    .lead {
        color: $simply-green--dark;
    }
}
