.social {

    &-section {
        padding-top: 4rem;

        @media #{$mq-small} {
            padding-top: 6rem;
        }

        @media #{$mq-large} {
            padding-top: 7rem; 
        }
    }
}

.hp__social {

    @media #{$mq-small} {
        margin-left: auto;
    }

    .list--inline {
        justify-content: center;
        align-items: center;

        li {
            margin-right: 3rem;

            &:last-child {
                margin-right: 0;
            }
        }
    }

    .social__link {

        svg {
            width: 3rem;
            height: 3rem;
            fill: $simply-green--dark;
        }
    }
}
