body {
    color: $gray-dark;
    line-height: $line-height-base;
    font-family: $font-family-brand;
    font-size: $font-size-base;
    @media #{$mq-medium} {
        font-size: $font-size-base*1.2;
        line-height: $line-height-base*1.2;
    }
    @media #{$mq-large} {
        font-size: $font-size-base*1.3;
    }
    @media #{$mq-xlarge} {
        font-size: $font-size-base*1.4;
    }
    @media #{$mq-xxlarge} {
        font-size: $font-size-base*1.6;
    }
}

h1, .heading--1,
h2, .heading--2,
h3, .heading--3,
h4, .heading--4,
h5, .heading--5,
h6, .heading--6 {
    font-weight: 700;
    margin: 0;

    & a {
        all: inherit;
        cursor: pointer; 
    }
}

.heading--1,
h1 {
    font-size: $font-size-h1;
    line-height: 1;
    margin-bottom: 0.875rem;
    @media #{$mq-medium} {
        font-size: $font-size-h1*1.2;
        line-height: 1.2;
    }
    @media #{$mq-large} {
        font-size: $font-size-h1*1.3;
    }
    @media #{$mq-xlarge} {
        font-size: $font-size-h1*1.4;
    }
    @media #{$mq-xxlarge} {
        font-size: $font-size-h1*1.6;
    }
}

.heading--2,
h2 {
    font-size: $font-size-h2;
    line-height: 1.2;
    margin-bottom: 1.125rem;
    @media #{$mq-medium} {
        font-size: $font-size-h2*1.2;
        line-height: $header-line-height*1.2;
    }
    @media #{$mq-large} {
        font-size: $font-size-h2*1.3;
    }
    @media #{$mq-xlarge} {
        font-size: $font-size-h2*1.4;
    }
    @media #{$mq-xxlarge} {
        font-size: $font-size-h2*1.6;
    }
}

.heading--3,
h3 {
    font-size: $font-size-h3;
    line-height: 1.3;
    margin-bottom: 1rem;
    @media #{$mq-medium} {
        font-size: $font-size-h3*1.2;
        line-height: $header-line-height*1.2;
    }
    @media #{$mq-large} {
        font-size: $font-size-h3*1.3;
    }
    @media #{$mq-xlarge} {
        font-size: $font-size-h3*1.4;
    }
    @media #{$mq-xxlarge} {
        font-size: $font-size-h3*1.6;
    }
}

.heading--4,
h4 {
    font-size: $font-size-h4;
    line-height: 1.4;
    margin-bottom: 1.125rem;
    @media #{$mq-medium} {
        font-size: $font-size-h4*1.2;
        line-height: $header-line-height*1.2;
    }
    @media #{$mq-large} {
        font-size: $font-size-h4*1.3;
    }
    @media #{$mq-xlarge} {
        font-size: $font-size-h4*1.4;
    }
    @media #{$mq-xxlarge} {
        font-size: $font-size-h4*1.6;
    }
}

.heading--5,
h5 {
    font-size: $font-size-h5;
    line-height: 1.5;
    margin-bottom: 0.75rem;
    @media #{$mq-medium} {
        font-size: $font-size-h5*1.2;
        line-height: $header-line-height*1.2;
    }
    @media #{$mq-large} {
        font-size: $font-size-h5*1.3;
    }
    @media #{$mq-xlarge} {
        font-size: $font-size-h5*1.4;
    }
    @media #{$mq-xxlarge} {
        font-size: $font-size-h5*1.6;
    }
}

.heading--6,
h6 {
    font-size: $font-size-h6;
    line-height: $line-height-base;
    margin-bottom: 0.6125rem;
    @media #{$mq-medium} {
        font-size: $font-size-h6*1.2;
        line-height: $header-line-height*1.2;
    }
    @media #{$mq-large} {
        font-size: $font-size-h6*1.3;
    }
    @media #{$mq-xlarge} {
        font-size: $font-size-h6*1.4;
    }
    @media #{$mq-xxlarge} {
        font-size: $font-size-h6*1.6;
    }
}

p {
    margin: 0 0 1rem;
    @media #{$mq-medium} {
        margin-bottom: 1.5rem;
    }
}

a {
    color: $sysco-green;
    transition: all 250ms ease-in-out;

    &:focus,
    &:hover,
    &:visited {
        color: darken($sysco-green, 15%);
    }
}

.text {
    &--large {
        font-size: $font-size-lg;
        @media #{$mq-medium} {
            font-size: $font-size-lg*1.2;
            line-height: $line-height-base*1.2;
        }
        @media #{$mq-large} {
            font-size: $font-size-lg*1.3;
        }
        @media #{$mq-xlarge} {
            font-size: $font-size-lg*1.4;
        }
        @media #{$mq-xxlarge} {
            font-size: $font-size-lg*1.6;
        }
    }
}

.lead {
    font-size: $font-size-h4;
    @media #{$mq-medium} {
        font-size: $font-size-h4*1.2;
        line-height: $header-line-height*1.2;
    }
    @media #{$mq-large} {
        font-size: $font-size-h4*1.3;
    }
    @media #{$mq-xlarge} {
        font-size: $font-size-h4*1.4;
    }
    @media #{$mq-xxlarge} {
        font-size: $font-size-h4*1.6;
    }
}

.title {
    display: block;

    a {
        all: inherit;
        cursor: pointer;
    }
}
