.simply {
    &-header {
         overflow-x: hidden;
         position: relative;
         border-bottom: 1px solid $simply-green--light;
         padding-bottom: 2rem;

        &__border {
            min-width: 1280px;
            margin: 0 auto;
            position: relative;
            z-index: 1;
        }

        &__logo {
            width: 100%;
            max-width: 280px;

            @media #{$mq-small} {
                max-width: none;
            }
        }

        &:before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;
            width: 16%;
            background-image: url('../assets/img/pattern-leaf_small.png');
            background-repeat: no-repeat;
            background-size: contain;
            background-position: top left;
            z-index: 0;
            display: none;

            @media #{$mq-small} {
                display: block;
            }
        }

    }
}
