.product {

    &-info {

        padding-bottom: 3rem;
        text-align: center;

        &__table {
            width: 100%;
            border-bottom: 1px solid $gray-dark;

            thead {
                color: $simply-green--dark;
                border-bottom: 1px solid $gray-dark;
            }



            tr:nth-of-type(even) {
                background-color: $simply-green--pale;
            }

            th,td {
                text-align: left;
                padding: 1rem 1.5rem;
            }

            .desc,
            .brand {
                text-transform: uppercase;
            }
        }

        .button {
            margin-top: $gutter*2;
        }
    }
}
