.list {

    $list: &;

    &--reset {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    &--block {
        @extend .list--reset;

        #{$list}__item {
            border-left: 5px solid $simply-green--dark;
            padding-left: 2rem;
            margin-bottom: 3rem;
        }
    }

    &--inline {
        @extend .list--reset;
        display: flex;
        flex-wrap: wrap;

        li {
            margin-right: 1.5rem;
            flex: 0 1 auto;

            &:last-child {
                margin-right: 0;
            }
        }

    }

    &--terms {
        @extend .list--reset;

        @media #{$mq-small} {
            li {
                display: inline-block;

                &:not(:first-child) {

                    &:before {
                        content: '|';
                        margin: 0 .5rem;
                    }
                }
            }
        }
    }
}
