.carousel {
    &-section {
        padding: 8rem 0;
        background: linear-gradient(160deg, $simply-green--dark 20%, $simply-green--light);
        position: relative;

        @media #{$mq-medium} {
            padding: 10rem 0;
        }

        .container {
            position: relative;
            z-index: 5;
        }

        .cutout {
            position: absolute;
            z-index: 1;

            &--watermelon {
                top: -5rem;
                left: 0;
                width: 240px;

                @media #{$mq-medium} {
                    width: 35%;
                    top: -8rem;
                    max-width: 450px;
                }
            }

            &--knife {
                top: 4rem;
                right: 0;
                width: 200px;
                display: none;

                @media #{$mq-xsmall} {
                    display: block;
                }

                @media #{$mq-medium} {
                    width: 35%;
                    max-width: 380px;
                }
            }

            &--legumes {
                bottom: 0;
                right: 0;
                width: 240px;
                display: block;

                @media #{$mq-medium} {
                    width: 25%;
                    max-width: 360px;
                    right: 4%;
                }



            }

            &--greenbeans {
                left: 0;
                bottom: 4rem;
                width: 140px;
                display: none;

                @media #{$mq-xsmall} {
                    display: block;
                }

                @media #{$mq-medium} {
                    width: 25%;
                    right: 4%;
                    max-width: 200px;
                }
            }
        }
    }

    &__title {
        text-align: center;
        color: $white;
        text-shadow: 0 1rem 2rem rgba(29, 49, 38, 0.5);
        margin-bottom: 4rem;

        @media #{$mq-xsmall} {
            margin-bottom: 6rem;
        }

        @media #{$mq-medium} {
            margin-bottom: 8rem;
        }
    }
}

.featured-carousel {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;

    > * {
        flex-grow: 1;
    }
}

.circle-slider {

    &__rotator {
        margin: 0 auto;
    }

    &__list {
        border-radius: 50%;
        border: 1px $simply-green--bright solid;
        width: 85%;
        height: 85%;
        margin: 0 auto;
        position: relative;
        transform: rotate(0deg);
        z-index: 0;
        padding: 0;
        max-width: 640px;
        max-height: 640px;

        @media #{$mq-xlarge} {
            max-width: 720px;
            max-height: 720px;
        }
    }

    &__item {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        display: block;
        z-index: 10;
        visibility: hidden;
    }

    &__block {
        visibility: visible;
        cursor: pointer;
        text-align: center;
        overflow: hidden;
        color: $white;
        width: 25%;
        margin: -12% auto;
        transition: all 500ms ease-in-out;

        @media #{$mq-xsmall} {
            width: 30%;
            margin: -15% auto;
        }

        @media #{$mq-medium} {
            width: 25%;
            margin: -12% auto;
        }

        @media #{$mq-xlarge} {
            width: 27.5%;
            margin: -14% auto;
        }
    }

    &__image {
        margin: auto;
        width: 100%;
    }

    .count2 li:nth-child(2) {
        transform: rotate(180deg);
    }

    .count3 li {
        &:nth-child(2) {
            transform: rotate(120deg);
        }

        &:nth-child(3) {
            transform: rotate(240deg);
        }
    }

    .count4 li {
        &:nth-child(2) {
            transform: rotate(90deg);
        }

        &:nth-child(3) {
            transform: rotate(180deg);
        }

        &:nth-child(4) {
            transform: rotate(270deg);
        }
    }

    .count5 li {
        &:nth-child(2) {
            transform: rotate(72deg);
        }

        &:nth-child(3) {
            transform: rotate(144deg);
        }

        &:nth-child(4) {
            transform: rotate(216deg);
        }

        &:nth-child(5) {
            transform: rotate(-72deg);
        }
    }

    .count6 li {
        &:nth-child(2) {
            transform: rotate(60deg);
        }

        &:nth-child(3) {
            transform: rotate(120deg);
        }

        &:nth-child(4) {
            transform: rotate(180deg);
        }

        &:nth-child(5) {
            transform: rotate(240deg);
        }

        &:nth-child(6) {
            transform: rotate(-60deg);
        }
    }

    .count7 li {
        &:nth-child(2) {
            transform: rotate(51.5deg);
        }

        &:nth-child(3) {
            transform: rotate(103deg);
        }

        &:nth-child(4) {
            transform: rotate(154.5deg);
        }

        &:nth-child(5) {
            transform: rotate(206deg);
        }

        &:nth-child(6) {
            transform: rotate(-103deg);
        }

        &:nth-child(7) {
            transform: rotate(-51.5deg);
        }
    }

    .count8 li {
        &:nth-child(2) {
            transform: rotate(45deg);
        }

        &:nth-child(3) {
            transform: rotate(90deg);
        }

        &:nth-child(4) {
            transform: rotate(135deg);
        }

        &:nth-child(5) {
            transform: rotate(180deg);
        }

        &:nth-child(6) {
            transform: rotate(-135deg);
        }

        &:nth-child(7) {
            transform: rotate(-90deg);
        }

        &:nth-child(8) {
            transform: rotate(-45deg);
        }
    }

    .count9 li {
        &:nth-child(2) {
            transform: rotate(40deg);
        }

        &:nth-child(3) {
            transform: rotate(80deg);
        }

        &:nth-child(4) {
            transform: rotate(120deg);
        }

        &:nth-child(5) {
            transform: rotate(160deg);
        }

        &:nth-child(6) {
            transform: rotate(-160deg);
        }

        &:nth-child(7) {
            transform: rotate(-120deg);
        }

        &:nth-child(8) {
            transform: rotate(-80deg);
        }

        &:nth-child(9) {
            transform: rotate(-40deg);
        }
    }

    .count10 li {
        &:nth-child(2) {
            transform: rotate(36deg);
        }

        &:nth-child(3) {
            transform: rotate(72deg);
        }

        &:nth-child(4) {
            transform: rotate(108deg);
        }

        &:nth-child(5) {
            transform: rotate(144deg);
        }

        &:nth-child(6) {
            transform: rotate(-180deg);
        }

        &:nth-child(7) {
            transform: rotate(-144deg);
        }

        &:nth-child(8) {
            transform: rotate(-108deg);
        }

        &:nth-child(9) {
            transform: rotate(-72deg);
        }

        &:nth-child(10) {
            transform: rotate(-36deg);
        }
    }

    div {
        position: relative;
        overflow: visible;
    }

    .slide {
        opacity: 0;
        visibility: hidden;
        transition: all 0.5s 0.4s;
        min-width: 131px;
        overflow: hidden;
        text-align: center;
        z-index: 9;
        position: absolute;
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        transform: translateY(-50%);
        padding: 1rem;
        color: $white;

        &-wrapper {
            width: 60%;
            height: 60%;
            position: absolute;
            top: 50%;
            left: 0;
            right: 0;
            transform: translateY(-50%);
            margin: auto;


            @media #{$mq-medium} {
                width: 40%;
                height: 40%;
            }

        }

        &__triangle {
            width: 0;
            height: 0;
            border-left: 1.25rem solid transparent;
            border-right: 1.25rem solid transparent;
            border-bottom: 1.25rem solid $simply-green--light;
            opacity: 0;
            transition: all 0.5s 0.4s;
            position: absolute;
            top: -2rem;
            left: 0;
            right: 0;
            margin: 0 auto;
        }

        &.active {
            opacity: 1;
            visibility: visible;

            .slide__triangle {
                opacity: 1;
            }
        }

        &__title {
            position: relative;
            display: block;
            font-size: 1.5rem;
            line-height: 1.2;
            font-weight: 700;
            margin-bottom: 0;

            a {
                all: inherit;
                text-decoration: none;
                cursor: pointer;
                transition: all 250ms ease-in-out;
            }

            @media #{$mq-xsmall} {
                font-size: 1.75rem;
            }

            @media #{$mq-small} {
                margin-bottom: 2rem;
            }

            @media #{$mq-medium} {
                font-size: 2.25rem;
                margin-bottom: 3rem;
            }

            &:hover {

                color: rgba(255,255,255,.9);

                &:after {
                    width: 124px;
                }
            }



            &:after {
                content: '';
                position: absolute;
                width: 100px;
                height: 3px;
                background-color: $simply-green--bright;
                margin: 0 auto;
                left: 0;
                right: 0;
                bottom: -1rem;
                display: none;
                transition: all 250ms ease-in-out;

                @media #{$mq-small} {
                    display: block;
                }

                @media #{$mq-medium} {
                    bottom: -1.5rem;
                }
            }
        }

        &__text {
            display: none;

            @media #{$mq-small} {
                display: block;
            }
        }
    }

    .next,
    .prev {
        display: none;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        margin: auto;
        width: 3rem;
        text-align: center;
        cursor: pointer;

        @media #{$mq-small} {
            display: block;
        }

        @media #{$mq-medium} {
            width: 4rem;
        }

        img {
            width: 100%;
        }
    }

    .prev {
        left: -1rem;
    }

    .next {
        right: -1rem;
    }
}
