.play {

    svg {
        width: 3rem;
        height: 3rem;
        transition: all 250ms ease-in-out; 

        @media #{$mq-medium} {
            width: 4.5rem;
            height: 4.5rem;
        }
    }

    &:hover,
    &:focus {

        svg {
            @media #{$mq-medium} {
                width: 5rem;
                height: 5rem;
            }
        }
    }

}
