.button {
    display: inline-block;
    padding: .5rem 1.5rem;
    border: 2px solid transparent;
    background-color: transparent;
    text-decoration: none;
    font-weight: 600;
    transition: all 250ms ease-in-out;
    background-color: $simply-green--dark;
    color: $white;

    &:visited {
        color: inherit;
    }

    &:hover,
    &:focus {
        background-color: $simply-green--light;
    }

    &--center {
        margin: 0 auto;
    }

    &--ghost {
        border-color: $white;
        color: $white;

        &:hover,
        &:focus {
            background-color: $simply-green--light;
        }
    }

    &--outline {
        border-color: $simply-green--dark;
        color: $simply-green--dark;
        background-color: transparent;

        &:visited {
            color: $simply-green--dark;
            background-color: transparent;
        }

        &:hover,
        &:focus {
            background-color: $simply-green--dark;
            color: $white;
        }
    }

    &--theme {
        background-color: $gray-dark;
        color: $white;

        &:hover,
        &:focus {
            background-color: darken($gray-dark, 10%);
        }

    }

    &--brand {
        border-color: $sysco-green;
        background-color: $sysco-green;
        color: $white;
        border-radius: 3px;
        text-transform: none;
        letter-spacing: 0;

        &:hover,
        &:focus {
            color: $white;
            border-color: darken($sysco-green, 10%);
            background-color: darken($sysco-green, 10%);
        }

        &:visited {
            color: $white;
        }
    }

    &--large {
        padding: .75rem 1.75rem;
        font-size: $font-size-lg;
    }

    &--small {
        padding: .25rem .5rem;
        font-size: $font-size-sm;
    }

    &--link {
        padding: .25rem .5rem;
        color: $white;
        background-color: lighten($simply-gray--light, 15%);

        &:visited {
            color: $white;
        }

        &:hover,
        &:focus {
            color: $white;
            background-color: lighten($simply-gray--light, 10%);
        }
    }
}
