.product {

    &-content {

        position: relative;
        padding: 3rem 0;
        background-color: $simply-green--pale;

        @media #{$mq-small} {
            margin-top: -2rem;
            padding: 8rem 0 12rem 0;
        }

        .menu {

            &__title {
                color: $white;
                background-color: $simply-green--dark;
                padding: .75rem 1rem;
                display: block;
                margin-bottom: 2rem;
            }

            &-list {

                .list__item {

                    padding-bottom: $gutter;
                    border-bottom: 1px solid $gray-dark;
                    margin-bottom: $gutter;

                    p:last-child {
                        margin: 0;
                    }

                    &:last-child {
                        border-bottom: 0;
                        margin: 0;
                    }
                }

            }
        }
    }
}

.simple-facts {

    margin-bottom: 2rem;

    &__title {
        display: block;
        margin-bottom: 3rem;
        position: relative;

        &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: -1.5rem;
            width: 4rem;
            height: 3px;
            background-color: $simply-green--light;
        }
    }

    .list__item {
        margin-right: 0;
        position: relative;

        &:not(:last-child) {

            &:after {
                content: ', ';
                white-space: pre;
            }
        }
    }
}
