.lets-talk {
    position: relative;
    padding: 4rem 0;
    background-color: $simply-green--bright;
    color: $simply-green--dark;

    @media #{$mq-large} {
        padding: 6rem 0;
    }

    &__heading {
            color: $simply-green--dark;
    }

    .container {
        position: relative;
        z-index: 3;
    }

    &__form {
        background-color: $white;
        padding: 1.5rem;
        box-shadow: 0 1rem 4rem 0 rgba(29, 49, 38, 0.33);
        color: $gray-dark;


        @media #{$mq-small} {
            position: absolute;
            left: 0;
            width: 100%;
            top: 50%;
            transform: translateY(-50%);
        }

        @media #{$mq-medium} {
            padding: 2rem;
        }

        @media #{$mq-large} {
            padding: 3rem;
        }
    }
}
