.breadcrumbs {
    padding: 1.5rem 0; 
    &__item {

        color: $gray-light;

        &:not(:last-child) {

            margin-right: 1rem;

            &:after {
                content: '|';
                margin-left: .5rem;
            }

        }
    }

    &__link {
        color: $gray-light;
        transition: all 250ms ease-in-out;
        font-weight: 700;
        text-decoration: none;

        &.active,
        &:hover,
        &:focus {
            color: $sysco-blue;
        }

        &:visited {
            color: $gray-light;
        }
    }
}
