.nav {

    $nav: &;

    display: flex;
    flex-wrap: wrap;
    align-items: center;

    @media #{$mq-medium} {
        padding: 0 1rem;
    }


    &__brand {
        padding: 1rem;
    }

    &__logo {
        display: block;
    }

    &__toggle {
        display: inline-block;
        margin-left: auto;
        justify-self: flex-end;
        margin-top: -.5rem;
        margin-right: 1rem;
        outline: none;

        .icon-bar {
            position: relative;
            border-radius: 0;
            background-color: #ffffff;
            display: block;
            height: 2px;
            transition: all 250ms ease-in-out;
            width: 22px;

            + .icon-bar {
                margin-top: 4px;
            }
        }

        @media #{$mq-medium} {
            display: none;
        }

        &.js-is-open {


            .icon-bar:nth-of-type(1) {
                top: 6px;
                transform: rotate(45deg);
            }
            .icon-bar:nth-of-type(2) {
                background-color: transparent!important;
            }
            .icon-bar:nth-of-type(3) {
                top: -6px;
                transform: rotate(-45deg);
            }
        }
    }

    &__drawer {
        display: none;
        flex: 1 1 auto;
        border-top: 1px solid #E4E5E6;

        &.js-is-open {
            display: block;
            width: 100%;
            background-color: $white;
        }

        @media #{$mq-medium} {
            border: 0;
            padding: 1rem;
            display: flex;
            align-items: center;
            flex: 1 1 auto;

        }
    }

    &__menu {
        list-style: none;
        margin: 0;
        padding: 0;
        font-size: $font-size-sm;
        font-weight: 600;
        text-align: center;

        @media #{$mq-medium} {
            display: flex;
            align-items: center;
            text-align: left;

        }

        @media #{$mq-large} {
            font-size: $font-size-base;

        }

        &--primary {

            #{$nav}__link {
                color: $sysco-blue;

                &:visited {
                    color: $sysco-blue;
                }
            }

            @media #{$mq-medium} {

                >#{$nav}__item {

                    #{$nav}__link {
                        color: $white;

                        &:visited {
                            color: $white;
                        }
                    }
                }
            }
        }

        &--secondary {

            .button {
                border-radius: 0;
                display: block;

                @media #{$mq-medium} {
                    border-radius: 3px;
                    display: inline-block;
                }
            }

            @media #{$mq-medium} {
                margin-left: auto;
                justify-self: flex-end;
            }
        }
    }

    &__item {
        position: relative;
        border-bottom: 1px solid #E4E5E6;

        @media #{$mq-medium} {
            margin-right: 1rem;
            border: 0;

            &:last-child {
                margin-right: 0;
            }
        }

        @media #{$mq-large} {
            margin-right: 1.5rem;
        }



        &[data-show-sub="true"] {

            .nav-arrow__container {
                transform: rotate(90deg);
            }

        }
    }

    &__link {
        text-decoration: none;
        display: block;
        padding: .75rem 1rem;

        @media #{$mq-medium} {
            padding: 1.5rem 0;
        }

        &--cta {

            font-weight: 600;

            &:visited {
                color: $sysco-green;
            }
        }
    }

    &-arrow {
        position: absolute;
        right: 0;
        top: 0;
        border-left: 1px solid #E4E5E6;
        padding: 19px;
        pointer-events: none;

        @media #{$mq-medium} {
            display: none;
        }

        &__container {
            transition: all 175ms ease-in-out;
        }

        &__bar {
            display: block;
            border-radius: 0;
            background-color: #008CD2;
            display: block;
            height: 2px;
            width: 11px;

            &:nth-of-type(1) {
                transform: rotate(45deg);
            }

            &:nth-of-type(2) {
                margin-top: 5px;
                transform: rotate(-45deg);
            }
        }
    }

    &-submenu {
        list-style: none;
        margin: 0;
        padding: 0;
        background-color: #F8F8F9;
        font-size: $font-size-sm;

        @media #{$mq-medium} {
            position: absolute;
            top: 100%;
            left: 0;
            min-width: 250px;
            padding: 1.25rem 1.75rem;
            border-radius: 6px;
            box-shadow: 0 0.5rem 1.5rem 0.2rem rgba(0, 0, 0, 0.35);
            z-index: 11;
        }

        &__heading,
        &__break {
            display: none;
            margin-bottom: .75rem;

            @media #{$mq-medium} {
                display: block;
            }
        }

        &__heading {
            color: $sysco-gray;
            font-weight: 700;
        }

        &__break {
            border-top: 1px solid #E4E5E6;
        }

        &__item {
            border-top: 1px solid #E4E5E6;

            @media #{$mq-medium} {
                border: 0;
                margin-bottom: .75rem;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        &__link {
            text-decoration: none;
            display: block;
            padding: .75rem 1rem;
            color: $sysco-blue;
            transition: all 250ms ease-in-out;

            @media #{$mq-medium} {
              font-weight: 400;
              padding: 0;
              color: $sysco-gray;
            }


            &:visited {
                color: $sysco-gray;
            }

            &:hover {

                @media #{$mq-medium} {
                  color: $sysco-green;
                }

            }
        }
    }
}

.nav-submenu {
    display: none;
}

[data-visually-hidden="true"] {
  display: none;
}
[data-visually-hidden="false"] {
  display: block;
}
