//
//  TYPOGRAPHY
//––––––––––––––––––––––––––––––––––––––––––––––––––

$font-size-base: 1rem;
$font-size-sm: .875rem;
$font-size-lg: 1.125rem;

$font-size-h1: $font-size-base * 2.75;
$font-size-h2: $font-size-base * 2.25;
$font-size-h3: $font-size-base * 1.75;
$font-size-h4: $font-size-base * 1.5;
$font-size-h5: $font-size-base * 1.25;
$font-size-h6: $font-size-base;

$line-height-base: 1.5;
$header-line-height: 1.25;

$font-family-brand: 'myriad-pro', 'Helvetica', 'Arial', sans-serif;

//
//  MEDIA QUERIES
//––––––––––––––––––––––––––––––––––––––––––––––––––


// Breakpoints
$bp-xsmall: 36em; // 576px
$bp-small: 48em; // 768px
$bp-medium: 62em; // 992px
$bp-large: 85.375em; // 1366px
$bp-xlarge: 120em; // 1920px
$bp-xxlarge: 160em; // 2560px

// Media Queries
$mq-xsmall: "(min-width: #{$bp-xsmall})";
$mq-small: "(min-width: #{$bp-small})";
$mq-medium: "(min-width: #{$bp-medium})";
$mq-large: "(min-width: #{$bp-large})";
$mq-xlarge: "(min-width: #{$bp-xlarge})";
$mq-xxlarge: "(min-width: #{$bp-xxlarge})";
$mq-retina: "(-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi)";

//
//  COLORS
//––––––––––––––––––––––––––––––––––––––––––––––––––

$sysco-green: #56b146;
$sysco-blue: #008cd2;
$sysco-gray: #6A737B;
$gray-dark: #2a2f33;
$gray-light: #a1a1a1;
$white: #ffffff;
$black: #000000;
$simply-green--pale: #E5FCD8;
$simply-green--bright: #CBF9B1;
$simply-green--light: #9DD19B;
$simply-green--dark: #00833E;
$simply-gray--light: #7E8C98;
$simply-gray--dark: #727F8A;

//
//  THEME
//––––––––––––––––––––––––––––––––––––––––––––––––––

$theme-primary: #213554;
$theme-secondary: #f0f1f4;
$theme-other: #797D82;

//
//  GRID
//––––––––––––––––––––––––––––––––––––––––––––––––––

$gap : 3.5rem; // space between columns
$gutter : $gap / 2;

//
//  OTHER
//––––––––––––––––––––––––––––––––––––––––––––––––––

$header-offset: 6rem; // height of header
