.sr-only {
    border:0;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
    height: 1px;
    margin: -1px;
    width: 1px;
    overflow: hidden;
    position: absolute !important;
    word-wrap:normal !important;
}

.w--100 {
    width: 100%;
}

.mb-0 {
    margin-bottom: 0 !important;
}

.mb-1 {
    margin-bottom: 1rem !important;
}

.m-0 {
    margin: 0;
}

.display--sm {
    display: none;

    @media #{$mq-small}  {
        display: block;
    }
}

.text {

    &--right {
        text-align: right;
    }

    &--lightgreen {
        color: $simply-green--light;
    }

    &--green {
        color: $simply-green--dark;
    }

}

.img--full {
    width: 100%;
}
