.grid {

    $grid: &;

    display: flex;
    flex-direction: column;
    margin-left: -$gutter;
    margin-right: -$gutter;
    position: relative;

    @media #{$mq-small} {
        flex-direction: row;
        flex-wrap: wrap;
    }

    &__item {
        width:100%;
    	margin-bottom: 2rem;
        padding: 0 $gutter;
        flex: 1 1 auto;

        @media #{$mq-small} {
            margin-bottom: 0;
            // loop for 12 columns
        	@for $i from 1 through 12 {
        		&--#{$i}{
        			max-width: ( $i * 100% / 12 ) ;
                    flex-basis: ( $i * 100% / 12 ) ;
        		}
        	}
        }

        &--push {
            margin-left: auto;
        }

        &--pull {
            margin-right: auto;
        }
    }

    &--center {
        align-items: center;
    }

}

.form-grid {

    display: flex;
    flex-direction: column;
    margin-left: -1rem;
    margin-right: -1rem;

    @media #{$mq-small} {
        flex-direction: row;
    }

    &__item {

        width:100%;
    	margin-bottom: 1rem;
        padding: 0 1rem;
        flex: 0 1 auto;

        @media #{$mq-small} {
            margin-bottom: 0;
            // loop for 12 columns
        	@for $i from 1 through 12 {
        		&--#{$i}{
        			max-width: ( $i * 100% / 12 ) ;
                    flex-basis: ( $i * 100% / 12 ) ;
        		}
        	}
        }
    }
}
