.modal {

    $modal: &;

    position: fixed;
    z-index: 10000; /* 1 */
    top: 0;
    left: 0;
    visibility: hidden;
    width: 100%;
    height: 100%;

    &.js-is-visible {
        visibility: visible;
    }

    &__overlay,
    &__container {
        position: fixed;
        z-index: 9000;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        visibility: hidden;
        opacity: 0;
        transition: visibility 0s ease 0.3s, opacity 0.3s ease;
    }

    &__overlay {
        background-color: rgba(255, 255, 255, 0.9);
    }

    &__wrapper {
        position: absolute;
        z-index: 10000;
        top: 2rem;
        left: 50%;
        width: 100%;
        max-width: 768px;
        margin-left: -50%;
        background-color: transparent;
        box-shadow: 0 0 1.5rem rbga(0, 0, 0, 0.35);
        padding: 0 1.5rem;

        @media #{$mq-small} {
            top: 4rem;
            margin-left: -384px;
        }

        @media #{$mq-medium} {
            top: 4rem;
            margin: 0 auto;
            max-width: 75%;
            left: 0;
            right: 0; 
        }
    }

    &__header {
        text-align: right;
        margin-bottom: 1rem;
    }

    &__close {
        color: $gray-dark;
        font-size: 1.5rem;
    }

    &--transition {
        transition: visibility 0.3s 0.12s, opacity 0.3s 0.12s, transform 0.3s 0.12s;
        transform: translateY(-10%);
        opacity: 0;
    }

    &.js-is-visible {

        #{$modal}__overlay,
        #{$modal}__container {
            opacity: 1;
            visibility: visible;
            transition-delay: 0s;
        }

        #{$modal}__container {
            overflow: auto; /* 1 */
            -webkit-overflow-scrolling: touch; /* 1 */
        }

        #{$modal}--transition {
            transform: translateY(0);
            opacity: 1;
        }
    }
}


.no-scroll {
  overflow: hidden;
}
